:root {
  --accent: #986B0D;
  --textColor: #312C93;
  --titleColor: #E30611;
  --journalism: Journalism, sans-serif;
  --mts: "Mts Sans";
}

@font-face {
  font-family: "Journalism";
  src: url("../fonts/journalism/journalism.eot");
  src: local("Journalism"), url("../fonts/journalism/journalism.eot?#iefix") format("embedded-opentype"), url("../fonts/journalism/journalism.woff2") format("woff2"), url("../fonts/journalism/journalism.woff") format("woff"), url("../fonts/journalism/journalism.ttf") format("truetype"), url("../fonts/journalism/journalism.otf") format("opentype"), url("../fonts/journalism/journalism.svg") format("svg");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Mts Sans";
  src: url("../fonts/mts/MTSSans-Regular.eot");
  src: local("Mts Sans"), url("../fonts/mts/MTSSans-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/mts/MTSSans-Regular.woff2") format("woff2"), url("../fonts/mts/MTSSans-Regular.woff") format("woff"), url("../fonts/mts/MTSSans-Regular.ttf") format("truetype"), url("../fonts/mts/MTSSans-Regular.otf") format("opentype"), url("../fonts/mts/MTSSans-Regular.svg") format("svg");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Mts Sans";
  src: url("../fonts/mts/MTSSans-Medium.eot");
  src: local("Mts Sans"), url("../fonts/mts/MTSSans-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/mts/MTSSans-Medium.woff2") format("woff2"), url("../fonts/mts/MTSSans-Medium.woff") format("woff"), url("../fonts/mts/MTSSans-Medium.ttf") format("truetype"), url("../fonts/mts/MTSSans-Medium.otf") format("opentype"), url("../fonts/mts/MTSSans-Medium.svg") format("svg");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Mts Sans";
  src: url("../fonts/mts/MTSSans-Bold.eot");
  src: local("Mts Sans"), url("../fonts/mts/MTSSans-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/mts/MTSSans-Bold.woff2") format("woff2"), url("../fonts/mts/MTSSans-Bold.woff") format("woff"), url("../fonts/mts/MTSSans-Bold.ttf") format("truetype"), url("../fonts/mts/MTSSans-Bold.otf") format("opentype"), url("../fonts/mts/MTSSans-Bold.svg") format("svg");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Mts Sans";
  src: url("../fonts/mts/MTSSans-Black.eot");
  src: local("Mts Sans"), url("../fonts/mts/MTSSans-Black.eot?#iefix") format("embedded-opentype"), url("../fonts/mts/MTSSans-Black.woff2") format("woff2"), url("../fonts/mts/MTSSans-Black.woff") format("woff"), url("../fonts/mts/MTSSans-Black.ttf") format("truetype"), url("../fonts/mts/MTSSans-Black.otf") format("opentype"), url("../fonts/mts/MTSSans-Black.svg") format("svg");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #FFFDF7;
  font-family: var(--mts), "Helvetica", Arial, sans-serif;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--accent);
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;
  border-radius: 10px;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 10px;
}

.container {
  width: 100%;
  max-width: 1330px;
  margin: 0 auto;
  padding: 0 25px;
}

.title {
  font-family: var(--journalism);
  font-weight: normal;
  font-size: 72px;
  line-height: 100%;
  color: var(--titleColor);
}

.star {
  position: absolute;
  animation-name: starsRotate;
  animation-duration: 10.5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.star__gold {
  animation-name: starsFlashing;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.star__blue {
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
}

@keyframes starsFlashing {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes starsRotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.header {
  padding-top: 30px;
  padding-bottom: 30px;
  position: relative;
}
.header__left {
  position: absolute;
  top: 0;
  left: 0;
}
.header__right {
  position: absolute;
  top: 0;
  right: 0;
}
.header__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 10;
}
.header__star-1 {
  top: 35%;
  left: 22%;
}
.header__star-2 {
  top: 17%;
  right: 20%;
}
.header__nav {
  margin-bottom: 80px;
}
.header__list {
  display: flex;
  justify-content: center;
  grid-gap: 26px;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  color: var(--textColor);
  font-family: "Roboto Condensed";
}
.header__link--active {
  color: var(--titleColor);
}
.header__mts {
  margin-bottom: 50px;
}
.header__logo {
  margin-bottom: 25px;
  margin-left: 42px;
}
.header__text {
  width: 100%;
  max-width: 700px;
  font-weight: 500;
  font-size: 18px;
  line-height: 179.69%;
  text-align: center;
}

.main {
  flex-grow: 1;
}

.clock {
  margin-bottom: 186px;
}
.clock__top {
  margin-bottom: 40px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  position: relative;
}
.clock__star-big {
  top: 8%;
  left: 48%;
}
.clock__star-small {
  right: 30%;
  top: 30%;
}
.clock__star-down {
  bottom: -60px;
  right: -5%;
}
.clock__time {
  min-width: 590px;
  width: 590px;
  height: 590px;
  position: relative;
}
.clock__days {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-weight: normal;
  font-size: 116px;
  line-height: 100%;
  text-align: center;
  color: var(--titleColor);
  white-space: nowrap;
  font-family: var(--journalism);
}
.clock__info {
  width: 100%;
  max-width: 530px;
  margin-bottom: 40px;
}
.clock__title {
  margin-bottom: 25px;
}
.clock__text {
  line-height: 180%;
}
.clock__wrapper {
  flex-wrap: wrap;
  grid-gap: 17px 20px;
}
.clock__item {
  width: 305px;
  min-width: 305px;
  height: 304px;
  padding: 17px;
  font-family: var(--journalism);
  text-transform: uppercase;
  font-weight: normal;
  font-size: 96px;
  line-height: 100%;
  color: var(--accent);
  position: relative;
}
.clock__item--active {
  color: var(--titleColor);
}
.clock__item-img {
  display: block;
  width: 100%;
  height: 100%;
}
.clock__item-day {
  position: absolute;
  z-index: 10;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  white-space: nowrap;
}

.photo {
  margin-bottom: 110px;
}
.photo__inner {
  padding-right: 221px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  position: relative;
}
.photo__star-1 {
  left: -10%;
  top: -10%;
}
.photo__star-2 {
  left: 50%;
  top: 10%;
}
.photo__star-3 {
  right: -10%;
  top: -30%;
}
.photo__send {
  max-width: 500px;
}
.photo__title {
  margin-bottom: 30px;
}
.photo__text {
  line-height: 180%;
}
.photo__download {
  width: 100%;
  max-width: 414px;
  margin-top: -20px;
  position: relative;
}
.photo__img {
  position: absolute;
  z-index: 10;
  right: -84%;
  top: -70%;
  max-width: none;
}
.photo__field {
  width: 100%;
  height: 204px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url("../images/icons/border.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  position: relative;
  font-weight: bold;
  font-size: 30px;
  line-height: 120%;
  color: var(--textColor);
}
.photo__field input {
  display: none;
}

.gifts {
  position: relative;
}
.gifts__star-1 {
  top: -12%;
  left: 38%;
}
.gifts__star-2 {
  top: -3%;
  right: 21%;
}
.gifts__img img {
  width: 100%;
}

footer {
  padding: 20px;
}